import Vue from 'vue';
import App from './App.vue';
import VueRouter from "vue-router";
Vue.use(VueRouter);
import VueResource from 'vue-resource'
Vue.use(VueResource);
const router = new VueRouter({
      // mode: 'history',//history  url page 
      routes: [
            {
                  path: '/',
                  component: resolve => require(['./components/automatic.vue'], resolve)
            }
      ]
})


new Vue({
      router:router,
      el: '#app',
      render: h => h(App)
})



